import { useContext, useEffect, useState } from "react";
import {
  Breadcrumb,
  Button,
  Form,
  Typography,
  Table,
  Modal,
  Space,
  Tag,
  Divider,
  Image,
  message,
  Select,
} from "antd";
import {
  Card,
  FlexContainer,
  FlexEnd,
  FlexHalf,
  FormRangePicker,
  FormSelect,
  FormTextArea,
} from "../../Components";
import withAuth from "../../HOC/withAuth";
import {
  useDisclosure,
  useGetData,
  useGetDataPagination,
  useUpdateData,
} from "../../Hooks";
import { AuthContext } from "../../Store/AuthContext";
import { FiFilter, FiCheck, FiX, FiNavigation } from "react-icons/fi";
import moment from "moment";
import { successColor, dangerColor, secondColor } from "../../Utils/colors";
import { myNumberFormat, setInput } from "../../Utils/myFormat";
import { RiFileExcel2Line } from "react-icons/ri";
import fileDownload from "js-file-download";
import { endpoint } from "../../Utils/enpoint";
import axios from "axios";

const { Title, Text } = Typography;
const { Option } = Select;

const ListContent = ({ label, children }) => (
  <div
    style={{ display: "flex", flexDirection: "column", marginBottom: "14px" }}
  >
    <Text type="secondary">{label}</Text>
    <Title level={5} style={{ marginTop: "0px" }}>
      {children}
    </Title>
  </div>
);

const ListDetails = ({ label, children }) => (
  <div style={{ display: "flex", flexDirection: "column" }}>
    <Text type="secondary">{label}</Text>
    <Text type="secondary" style={{ marginTop: "0px", marginBottom: "14px" }}>
      {children}
    </Text>
  </div>
);

const ListStatus = ({ label, status, children }) => {
  let statusColor;
  if (status === 0) {
    statusColor = secondColor;
  } else if (status === 1) {
    statusColor = successColor;
  } else {
    statusColor = dangerColor;
  }

  return (
    <div
      style={{ display: "flex", flexDirection: "column", marginBottom: "14px" }}
    >
      <Text type="secondary">{label}</Text>
      <div style={{ flexDirection: "row" }}>
        <Tag color={statusColor}>{children}</Tag>
      </div>
    </div>
  );
};

const ListImageGallery = ({ label, data }) => (
  <div
    style={{ display: "flex", flexDirection: "column", marginBottom: "14px" }}
  >
    <Text type="secondary">{label}</Text>
    <Image.PreviewGroup>
      <Space wrap>
        {data?.map((item) => (
          <div
            key={item.image}
            style={{ width: 120, height: 70, overflow: "hidden" }}
          >
            <Image src={item.image} width={120} alt="image-reimbursement" />
          </div>
        ))}
      </Space>
    </Image.PreviewGroup>
  </div>
);

const myDate = (date) => {
  return moment(date).format("YYYY-MM-DD");
};

const Reimbursement = () => {
  const { signOut } = useContext(AuthContext);
  const authContext = useContext(AuthContext);
  const [configData, setConfigData] = useState({
    page: 1,
    fromdate: myDate(authContext.firstDate),
    todate: myDate(authContext.lastDate),
    update: false,
  });
  const [perPage, setPerPage] = useState(20);
  const [empId, setEmpId] = useState("");

  const { data, pagination, loading } = useGetDataPagination(
    `reimburstment?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}&page=${configData.page}&perpage=${perPage}`,
    configData.update
  );

  const { data: dataEmployee, loading: loadingEmployee } =
    useGetData("employee");

  const [date, setDate] = useState([
    myDate(authContext.firstDate),
    myDate(authContext.lastDate),
  ]);
  const [remark, setRemark] = useState("");
  const [dataTemp, setDataTemp] = useState({});
  const [loadingExport, setLoadingExport] = useState(false);

  const [form] = Form.useForm();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const {
    isOpen: isRemark,
    onOpen: openRemark,
    onClose: closeRemark,
  } = useDisclosure();

  useEffect(() => {
    form.setFieldsValue({
      date: [moment(authContext.firstDate), moment(authContext.lastDate)],
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleFilterDate = () => {
    if (date.length === 0) {
      console.log("Form tidak bolah kosong");

      return null;
    }

    setConfigData({
      ...configData,
      page: 1,
      fromdate: date[0],
      todate: date[1],
    });
  };

  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess, loading: loadingApproval } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handleApprove = (appr) => {
    if (appr === 2 && !remark) {
      message.warn("Form tidak boleh kosong!");

      return null;
    }

    const dataAppr = {
      status: appr,
      reason: remark,
    };
    console.log("data post: ", dataAppr);

    setDataUpdate({
      url: `reimburstment/update?id=${dataTemp.id}`,
      data: dataAppr,
      trigger: Date.now(),
    });
  };

  useEffect(() => {
    if (onSuccess) {
      message.success("Proses approval berhasil");
      closeRemark();
      onClose();
      setConfigData({
        ...configData,
        update: !configData.update,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onSuccess]);

  const ExportToExcel = async () => {
    setLoadingExport(true);
    try {
      const response = await axios.get(
        `${endpoint}/api/reimburstment/export?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}`,
        {
          headers: {
            Authorization: `bearer ${authContext.userToken}`,
          },
          responseType: "blob",
        }
      );

      if (!response) {
        throw new Error();
      }

      message.success("Download in progress");
      fileDownload(response.data, "Reimbursement.xlsx");
      console.log(`fetch data /reimburstment/export?emp_id=${empId}&from=${configData.fromdate}&to=${configData.todate}`);
      setLoadingExport(false);
    } catch (err) {
      if (err.response?.status === 500) {
        message.error(`${err.response.status}, ${err.response.statusText}`);
        signOut();

        return null;
      }

      if (err.response) {
        message.error(`${err.response.status}, ${err.response.statusText}`);
        setLoadingExport(false);

        return null;
      }

      message.error("Something went wrong, please try again..");
      console.log(err);
      setLoadingExport(false);
    }
  };

  const columns = [
    {
      title: "#",
      dataIndex: "document_no",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => {
            setDataTemp(record);
            onOpen();
          }}
        >
          {text}
        </Text>
      ),
      width: 140,
    },
    {
      title: "Title",
      dataIndex: "title",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => {
            setDataTemp(record);
            onOpen();
          }}
        >
          {text}
        </Text>
      ),
      ellipsis: true,
    },
    {
      title: "Note",
      dataIndex: "note",
      ellipsis: true,
      responsive: ["lg"],
    },
    {
      title: "Total",
      dataIndex: "total",
      align: "right",
      render: (text) => myNumberFormat(text),
    },
    {
      title: "Status",
      dataIndex: "status_name",
      render: (text, record) => {
        let bg = "";
        if (record.status_id === 0) {
          bg = secondColor;
        } else if (record.status_id === 1) {
          bg = successColor;
        } else {
          bg = dangerColor;
        }

        return <Tag color={bg}>{text}</Tag>;
      },
    },
    {
      title: "Employee",
      dataIndex: "employee",
    },
    {
      title: "Date",
      dataIndex: "date",
    },
  ];
  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>Reimbursement</Breadcrumb.Item>
      </Breadcrumb>
      <Modal
        title="Details Reimbursement"
        visible={isOpen}
        onCancel={onClose}
        footer={false}
        width={1000}
      >
        <FlexContainer>
          <FlexHalf>
            <ListContent label="Document No">
              {dataTemp.document_no}
            </ListContent>
            <ListContent label="Title">{dataTemp.title}</ListContent>
            {dataTemp.title === "bbm_ops" && (
              <ListContent label="KM Mobil">{dataTemp.km_mobil}</ListContent>
            )}
            {dataTemp.title === "bbm_genset" && (
              <>
                <ListContent label="Genset Meter Start">
                  {dataTemp.genset_start}
                </ListContent>
                <ListContent label="Genset Meter End">
                  {dataTemp.genset_end}
                </ListContent>
                <ListContent label="Genset Meter Total">
                  {dataTemp.genset_total}
                </ListContent>
                <ListContent label="No Genset">
                  {dataTemp.no_genset}
                </ListContent>
              </>
            )}
            <ListContent label="Date">{dataTemp.date}</ListContent>
          </FlexHalf>
          <FlexHalf>
            <ListStatus label="Status" status={dataTemp.status_id}>
              {dataTemp.status_name}
            </ListStatus>
            <ListContent label="Employee">{dataTemp.employee}</ListContent>
            <ListDetails label="Note">{dataTemp.note}</ListDetails>
            <ListContent label="Total">{dataTemp.total}</ListContent>
          </FlexHalf>
        </FlexContainer>
        <ListImageGallery label="Photos" data={dataTemp.attachment} />
        {dataTemp.status_id === 0 && (
          <>
            <Divider />
            <FlexEnd>
              <Space>
                <Button
                  shape="round"
                  type="primary"
                  icon={<FiCheck />}
                  onClick={() => handleApprove(1)}
                >
                  Approve
                </Button>
                <Button shape="round" icon={<FiX />} onClick={openRemark}>
                  Disapprove
                </Button>
              </Space>
            </FlexEnd>
          </>
        )}
      </Modal>
      <Modal
        title="Remark Disapprove"
        visible={isRemark}
        onCancel={closeRemark}
        footer={false}
      >
        <Form name="remarkdisapprove" autoComplete="off">
          <FormTextArea
            label="Remark Disapprove"
            requiredLabel
            name="remark"
            required
            message="please input remark disapprove!"
            rows={4}
            placeholder="input remark disapprove"
            onChange={(e) => setInput(e, setRemark)}
          />
          <FlexEnd>
            <Form.Item>
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation />}
                onClick={() => handleApprove(2)}
                loading={loadingApproval}
                htmlType="submit"
              >
                Submit
              </Button>
            </Form.Item>
          </FlexEnd>
        </Form>
      </Modal>
      <div>
        <Card>
          <Title level={4} type="secondary">
            Reimbursement
          </Title>
          <FlexContainer>
            <Form form={form} name="form-reimbursement" autoComplete="off">
              <div style={{ display: "flex", flexDirection: "row" }}>
                <FormRangePicker
                  name="date"
                  required
                  message="please select date"
                  onChange={(date, dateString) => setDate(dateString)}
                  format="YYYY-MM-DD"
                />
                <Form.Item>
                  <Button
                    type="primary"
                    shape="round"
                    icon={<FiFilter style={{ marginRight: "6px" }} />}
                    onClick={handleFilterDate}
                    loading={loading}
                    htmlType="submit"
                    style={{ marginLeft: 16 }}
                  >
                    Show
                  </Button>
                </Form.Item>
              </div>
            </Form>
            <FlexEnd style={{ marginBottom: 14 }}>
              <Button
                shape="round"
                icon={<RiFileExcel2Line style={{ marginRight: "6px" }} />}
                onClick={ExportToExcel}
                loading={loadingExport}
              >
                Export
              </Button>
            </FlexEnd>
          </FlexContainer>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <FormSelect
              name="empId"
              placeholder="filter employee"
              onChange={(e) => setEmpId(e)}
              loading={loadingEmployee}
              disabled={loadingEmployee}
              style={{ minWidth: 290 }}
            >
              {dataEmployee.map((item) => (
                <Option value={item.id} key={item.id}>
                  {`[${item.id}] ${item.name}`}
                </Option>
              ))}
            </FormSelect>
          </div>
          <Table
            columns={columns}
            dataSource={data}
            rowKey={(record) => record.id}
            loading={loading}
            scroll={{ y: 500 }}
            pagination={{
              total: pagination.total_data,
              showTotal: (total, range) =>
                `${range[0]}-${range[1]} of ${total} items`,
              showQuickJumper: true,
              defaultCurrent: 1,
              onChange: (pageNumber) =>
                setConfigData({
                  ...configData,
                  page: pageNumber,
                }),
              showSizeChanger: true,
              onShowSizeChange: (current, pageSize) => setPerPage(pageSize),
              defaultPageSize: perPage,
              pageSizeOptions: [20, 60, 100, 140],
            }}
          />
        </Card>
      </div>
    </div>
  );
};

export default withAuth(Reimbursement);
